/*
 * Modul: Badge.
 * Definiert das Verhalten von Badges.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.badge {
  font-weight: bold;
  text-align: center;
  position: absolute;
  display: inline-block;
  top: 50%;
  right: 0;
  font-size: 12px;
  padding: 0 10px;
  height: 20px;
  line-height: 20px;
  background-color: #e5eaeb;
  color: #4b4e4f;
  border-radius: 10px 10px 10px 10px;
  transform: translate3d(0, -50%, 0);
  transition: all 0.15s ease 0s;
}
.badge--disabled {
  opacity: 0.3;
}
.badge--highlight {
  background-color: #d0ccf8;
  color: #5e5c70;
}
.badge--highlight:before {
  content: "\e81b";
  font-family: "Fontello", sans-serif;
}
.badge--highlight:before {
  font-weight: 100;
  margin-right: 4px;
  vertical-align: 1px;
  font-size: 10px;
}
/*
 * Modul: Body.
 * Definiert grundlegende Styles f√ºr den Body und sonstige Kleinigkeiten.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
:not(input:not([readonly])) {
  -webkit-touch-callout: none;
  user-select: none;
}
.body {
  -webkit-highlight: none;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background: #e5eaeb;
  font-family: "Lato", sans-serif;
}
@media screen and (min-width: 520px) {
  .body {
    background: #c8d3d5;
  }
}
/*
 * Modul: Button.
 * Definiert das Verhalten von Buttons.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.button {
  display: block;
  cursor: pointer;
  overflow: hidden;
  font-weight: bold;
  font-style: normal;
  position: relative;
  text-align: center;
  box-sizing: border-box;
  text-decoration: none;
  padding: 1px 44px;
  margin-bottom: 8px;
  height: 44px;
  line-height: 40px;
  font-size: 16px;
  box-shadow: 0 2px 2px 0 rgba(8, 54, 65, 0.05);
  background-color: #f1f3f4;
  color: #4b4e4f;
  background-image: linear-gradient(to bottom, #ffffff 0%, #f1f3f4 100%);
  border-radius: 2px 2px 2px 2px;
  border: 1px solid #c8d3d5;
  border-bottom-color: #bac7ca;
  transition: all 0.15s ease 0s;
}
.button:before {
  display: block;
  position: absolute;
  text-align: center;
  visibility: hidden;
  font-weight: normal;
  top: 0;
  left: 0;
  height: 100%;
  content: "";
  font-size: 15px;
  color: rgba(67, 82, 87, 0.5);
  padding: 1px 0;
  opacity: 0;
  width: 44px;
  transition: all 0.15s ease 0s;
}
.button:not(.button--inline):last-child {
  margin-bottom: 0;
}
.button--small {
  padding-left: 36px;
  padding-right: 36px;
  font-size: 15px;
  height: 36px;
  line-height: 32px;
  min-width: 40px;
}
.button--small:before {
  width: 36px;
}
.button--inline {
  display: inline-block;
  padding-left: 0;
  padding-right: 0;
  margin: 0 2px;
  margin-bottom: 4px;
  width: 48px;
}
.button--inline:before {
  display: none;
}
.button--inline.button--small {
  width: 40px;
}
.button--backspace {
  width: auto;
  text-transform: uppercase;
  font-size: 13px;
  padding-left: 16px;
  padding-right: 16px;
}
.button--image {
  float: left;
  font-size: 0;
  padding-left: 0;
  padding-right: 0;
  margin-bottom: 0;
  background: #ffffff;
  height: 110px;
  margin-top: 3%;
  width: 48.5%;
  line-height: 106px;
}
.button--image:nth-child(1),
.button--image:nth-child(2) {
  margin-top: 0;
}
.button--image:nth-child(odd) {
  margin-right: 3%;
}
.button--image:before {
  top: auto;
  bottom: 0;
  width: 30px;
  height: 30px;
  line-height: 30px;
}
.button--round {
  margin: 16px auto;
  font-size: 28px;
  width: 128px;
  height: 128px;
  padding: 40px 0;
  line-height: 23px;
  border-radius: 50% 50% 50% 50%;
}
.button--action {
  text-transform: uppercase;
  background-image: linear-gradient(to bottom, #5dcc91 0%, #3bbb77 100%);
  box-shadow: 0 2px 2px 0 rgba(8, 54, 65, 0.25);
  border: 1px solid #34a569;
  border-bottom-color: #2e915c;
  background-color: #3bbb77;
  color: #4b4e4f;
  text-shadow: 0 1px 0 rgba(8, 54, 65, 0.25);
}
.button--danger {
  text-transform: uppercase;
  background-image: linear-gradient(to bottom, #dd577d 0%, #d9426d 100%);
  box-shadow: 0 2px 2px 0 rgba(8, 54, 65, 0.25);
  border: 1px solid #ca3d65;
  border-bottom-color: #ba335a;
  background-color: #d9426d;
  color: #ffffff;
  text-shadow: 0 1px 0 rgba(8, 54, 65, 0.25);
}
.button--success,
.button--error,
.button--success:before,
.button--error:before {
  padding-top: 0;
  padding-bottom: 0;
}
.button--success:before,
.button--error:before {
  visibility: visible;
  opacity: 1;
}
.button--right:before {
  content: "\e81a";
  font-family: "Fontello", sans-serif;
}
.button--right:before {
  color: #3bbb77;
}
.button--wrong:before {
  content: "\e819";
  font-family: "Fontello", sans-serif;
}
.button--wrong:before {
  color: #d9426d;
}
.button--success {
  border: 2px solid #3bbb77;
  animation: answer-success 0.15s normal forwards;
}
.button--error {
  border: 2px solid #d9426d;
  animation: answer-error 0.15s normal forwards;
}
.button--locked {
  cursor: default;
  opacity: 0.3;
}
.button--margin-bottom {
  margin-bottom: 16px;
}
.button--round .button__text:last-child {
  font-size: 15px;
}
/*
 * Modul: Diagramm.
 * Definiert das Verhalten von Diagrammen.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.chart {
  overflow: hidden;
  position: relative;
  padding-left: 16px;
  padding-right: 128px;
  background-color: #f1f3f4;
  color: #4b4e4f;
  background-image: linear-gradient(to bottom, #ffffff 0%, #f1f3f4 100%);
}
.chart--vertical {
  padding-left: 0;
  padding-right: 0;
}
.chart__container {
  display: block;
  box-sizing: border-box;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 10px 0;
}
.chart--vertical,
.chart--vertical .chart__container {
  padding-top: 16px;
  padding-bottom: 40px;
}
.chart--vertical .chart__container {
  position: absolute;
  padding-left: 16px;
  padding-right: 16px;
}
.chart__lines {
  position: absolute;
  height: 100%;
  left: 16px;
  right: 128px;
}
.chart--vertical .chart__lines {
  position: static;
  left: auto;
  right: auto;
}
.chart__line {
  display: block;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  height: 100%;
  width: 1px;
  margin-left: -1px;
  background-image: linear-gradient(to bottom, #e5eaeb 0%, #f1f3f4 100%);
}
.chart__line:nth-child(1) {
  left: 10%;
}
.chart__line:nth-child(2) {
  left: 20%;
}
.chart__line:nth-child(3) {
  left: 30%;
}
.chart__line:nth-child(4) {
  left: 40%;
}
.chart__line:nth-child(5) {
  left: 50%;
}
.chart__line:nth-child(6) {
  left: 60%;
}
.chart__line:nth-child(7) {
  left: 70%;
}
.chart__line:nth-child(8) {
  left: 80%;
}
.chart__line:nth-child(9) {
  left: 90%;
}
.chart__line:nth-child(10) {
  left: 100%;
}
.chart--vertical .chart__line {
  width: 100%;
  float: none;
  position: static;
  height: 14px;
  border-top: 1px solid #e5eaeb;
  background-image: none;
}
.chart--vertical .chart__line:nth-child(1) {
  opacity: 0.1;
}
.chart--vertical .chart__line:nth-child(2) {
  opacity: 0.2;
}
.chart--vertical .chart__line:nth-child(3) {
  opacity: 0.3;
}
.chart--vertical .chart__line:nth-child(4) {
  opacity: 0.4;
}
.chart--vertical .chart__line:nth-child(5) {
  opacity: 0.5;
}
.chart--vertical .chart__line:nth-child(6) {
  opacity: 0.6;
}
.chart--vertical .chart__line:nth-child(7) {
  opacity: 0.7;
}
.chart--vertical .chart__line:nth-child(8) {
  opacity: 0.8;
}
.chart--vertical .chart__line:nth-child(9) {
  opacity: 0.9;
}
.chart--vertical .chart__line:nth-child(10) {
  opacity: 1;
}
.chart--vertical .chart__line:first-child {
  border-top: none;
}
.chart__data {
  display: block;
  position: relative;
  width: 100%;
  padding: 6px 0;
  opacity: 0;
  transform: translate3d(-180%, 0, 0);
  transition: all 0.15s ease 0s;
}
.chart__data:nth-child(1) {
  transition: all 0.5s ease 0s;
}
.chart__data:nth-child(2) {
  transition: all 0.45s ease 0s;
}
.chart__data:nth-child(3) {
  transition: all 0.4s ease 0s;
}
.chart__data:nth-child(4) {
  transition: all 0.35s ease 0s;
}
.chart__data:nth-child(5) {
  transition: all 0.3s ease 0s;
}
.chart__data:nth-child(6) {
  transition: all 0.25s ease 0s;
}
.chart__data:nth-child(7) {
  transition: all 0.2s ease 0s;
}
.chart__data:nth-child(8) {
  transition: all 0.15s ease 0s;
}
.chart__data:nth-child(9) {
  transition: all 0.1s ease 0s;
}
.chart__data:nth-child(10) {
  transition: all 0.05s ease 0s;
}
.chart--vertical .chart__data {
  float: left;
  padding: 0;
  height: 100%;
  width: 10%;
  transform: translate3d(0, 125%, 0);
}
.chart--grow .chart__data {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.chart__bar {
  display: block;
  box-sizing: border-box;
  max-width: 100%;
  height: 18px;
  background-image: linear-gradient(to bottom, #e9e352 0%, #e1da18 100%);
  border: 1px solid #bfb914;
  border-bottom: 1px solid #aca712;
  border-radius: 2px 2px 2px 2px;
  box-shadow: 0 2px 2px 0 rgba(8, 54, 65, 0.15);
}
.chart--vertical .chart__bar {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 18px;
  margin-left: -9px;
}
.chart--score .chart__bar {
  border-color: #34a569;
  border-bottom-color: #2f945e;
  background-image: linear-gradient(to bottom, #5dcc91 0%, #3bbb77 100%);
}
.chart__label {
  display: block;
  position: absolute;
  box-sizing: border-box;
  font-weight: bold;
  top: 0;
  height: 100%;
  font-size: 13px;
  line-height: 30px;
  padding: 0 16px;
  width: 128px;
  right: -128px;
}
.chart--vertical .chart__label {
  text-align: center;
  left: 0;
  top: auto;
  right: auto;
  width: 100%;
  padding: 0;
  height: 24px;
  line-height: 24px;
  bottom: -24px;
}
.chart__text {
  display: block;
  float: left;
}
.chart__text--stars {
  font-weight: normal;
}
.chart__text--percent {
  float: right;
  font-style: italic;
  text-align: right;
  font-size: 9px;
  color: rgba(67, 82, 87, 0.5);
  margin-top: 1px;
  width: 24px;
}
.chart__text--value {
  float: right;
  text-align: right;
  min-width: 24px;
  padding: 0 4px;
}
.chart--vertical .chart__text {
  float: none;
}
.chart__data--none .chart__bar {
  width: 3px !important;
}
.chart__data--none .chart__text {
  color: rgba(67, 82, 87, 0.5);
}
.chart__data--none .chart__label {
  color: rgba(67, 82, 87, 0.5);
}
.chart--vertical .chart__data--none .chart__bar {
  border: none;
  left: 0;
  margin: 0;
  text-align: center;
  background: transparent;
  width: 100% !important;
  height: auto !important;
  font-size: 13px;
  color: rgba(67, 82, 87, 0.5);
  box-shadow: none;
}
.chart--vertical .chart__data--none .chart__bar:after {
  content: "\e813";
  font-family: "Fontello", sans-serif;
}
/*
 * Modul: Dropdown.
 * Definiert das Verhalten eines Dropdowns.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.dropdown {
  display: block;
  position: absolute;
  visibility: hidden;
  list-style-type: none;
  left: 0;
  top: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
  z-index: 500;
  opacity: 0;
  border-bottom: 1px solid rgba(8, 54, 65, 0.25);
  transform: translate3d(0, -150%, 0);
  transition: all 0.15s ease 0s;
  box-shadow: 0 8px 16px 0 rgba(8, 54, 65, 0.15);
}
.dropdown--opened {
  visibility: visible;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.dropdown__item {
  display: block;
  cursor: pointer;
  position: relative;
  margin: 0;
  padding: 0 16px;
  padding-right: 44px;
  height: 44px;
  line-height: 44px;
  font-size: 13px;
  background-color: #ffffff;
  color: rgba(67, 82, 87, 0.5);
  transition: color 0.15s ease 0s;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.dropdown__item:after {
  content: "\e81a";
  font-family: "Fontello", sans-serif;
}
.dropdown__item:after {
  display: block;
  position: absolute;
  text-align: center;
  color: transparent;
  top: 0;
  right: 0;
  font-size: 15px;
  height: 44px;
  width: 44px;
  transition: color 0.15s ease 0s;
}
.dropdown__item:not(:last-child) {
  border-bottom: 1px solid #c8d3d5;
}
.dropdown__item--selected {
  cursor: default;
  background-color: #f1f3f4;
  color: #4b4e4f;
}
.dropdown__item--selected:after {
  color: #3bbb77;
}
.dropdown__icon {
  margin-right: 8px;
  font-size: 15px;
}
.dropdown__label {
  font-weight: bold;
}
/*
 * Modul: Liste.
 * Definiert das Verhalten einer Liste.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.list {
  display: block;
  list-style-type: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #c8d3d5;
}
.list__item {
  display: block;
  cursor: pointer;
  text-align: left;
  font-style: normal;
  position: relative;
  text-decoration: none;
  margin: 0;
  padding: 0 16px;
  padding-right: 44px;
  height: 44px;
  line-height: 44px;
  vertical-align: 1px;
  background-color: #f1f3f4;
  color: #4b4e4f;
  background-image: linear-gradient(to bottom, #ffffff 0%, #f1f3f4 100%);
  border-top: 1px solid #c8d3d5;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.list__item:after {
  display: block;
  position: absolute;
  text-align: right;
  font-style: normal;
  font-weight: normal;
  box-sizing: border-box;
  top: 0;
  right: 0;
  content: "\e816";
  padding-right: 16px;
  font-size: 15px;
  width: 44px;
  height: 44px;
  line-height: 45px;
  color: rgba(67, 82, 87, 0.5);
  transition: color 0.15s ease 0s;
  font-family: "Fontello", sans-serif;
}
.list__item-label {
  font-weight: bold;
}
.list__item-label:not(:first-child) {
  margin-left: 8px;
}
.list__item-highlight {
  background-color: #d0ccf8;
  color: #5e5c70;
  border-radius: 2px 2px 2px 2px;
}
.list__item-highlight:empty {
  display: none;
}
.list__item-icon {
  text-align: center;
  color: rgba(67, 82, 87, 0.5);
  margin-right: 8px;
  width: 16px;
}
.list__item-icon + .list__item-label {
  margin-left: 0;
}
.list__title {
  display: block;
  font-weight: bold;
  position: relative;
  padding: 16px;
  font-size: 15px;
  margin-bottom: 8px;
  line-height: 20px;
  background-color: #ffffff;
  color: rgba(67, 82, 87, 0.5);
  border-bottom: 1px solid #c8d3d5;
  border-top: 1px solid #c8d3d5;
}
.list__title:last-child {
  border-bottom: none;
  margin-bottom: 0;
}
.list__title-text {
  display: block;
  position: relative;
}
.list__title-icon {
  text-align: center;
  margin-right: 8px;
  width: 16px;
}
/*
 * Modul: Navigation-Bar.
 * Definiert die Navigation-Bar der App.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.navigationbar {
  position: absolute;
  height: auto;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  height: 44px;
  border-bottom: 1px solid #34a569;
  background-color: #3bbb77;
  color: #4b4e4f;
}
.navigationbar--webapp {
  height: 64px;
}
.navigationbar--webapp .navigationbar__wrapper {
  padding-top: 20px;
}
.navigationbar--search .navigationbar__search {
  opacity: 1;
}
.navigationbar--search .navigationbar__heading {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}
.navigationbar__wrapper {
  overflow: hidden;
  position: relative;
  text-align: center;
  z-index: 1000;
  height: 44px;
  border-bottom: 1px solid #34a569;
  background-color: #3bbb77;
  color: #4b4e4f;
}
.navigationbar__heading {
  font-weight: bold;
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  text-transform: uppercase;
  margin: 0;
  width: 100%;
  z-index: 1000;
  padding: 0 44px;
  height: 44px;
  line-height: 44px;
  font-size: 17px;
  transition: all 0.15s ease 0s;
  text-shadow: 0 1px 0 rgba(8, 54, 65, 0.25);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.navigationbar__heading--hidden {
  opacity: 0;
}
.navigationbar__button {
  display: block;
  cursor: pointer;
  position: absolute;
  text-align: center;
  font-style: normal;
  font-weight: 100;
  text-decoration: none;
  bottom: 0;
  margin: 0;
  padding: 0;
  z-index: 1500;
  font-size: 20px;
  height: 44px;
  line-height: 43px;
  width: 44px;
  transition: all 0.15s ease 0s;
  text-shadow: 0 1px 0 rgba(8, 54, 65, 0.25);
}
.navigationbar__button--left {
  left: 0;
}
.navigationbar__button--right {
  right: 0;
}
.navigationbar__button--disabled {
  cursor: default;
  opacity: 0;
}
.navigationbar__button--icon-cancel:after {
  content: "\e819";
  font-family: "Fontello", sans-serif;
}
.navigationbar__button--icon-skip:after {
  content: "\e808";
  font-family: "Fontello", sans-serif;
}
.navigationbar__button--icon-back:after {
  content: "\e815";
  font-family: "Fontello", sans-serif;
}
.navigationbar__button--icon-search:after {
  content: "\e81b";
  font-family: "Fontello", sans-serif;
}
.navigationbar__button--icon-sort:after {
  content: "\e811";
  font-family: "Fontello", sans-serif;
}
.navigationbar__search {
  overflow: hidden;
  position: absolute;
  background: transparent;
  margin: 0;
  padding: 0;
  z-index: 500;
  left: 44px;
  right: 44px;
  bottom: 8px;
  height: 28px;
  opacity: 0;
  transition: opacity 0.15s ease 0s;
}
.navigationbar__search:before {
  display: block;
  font-weight: normal;
  position: absolute;
  text-align: center;
  left: 0;
  bottom: 0;
  z-index: 2000;
  width: 28px;
  height: 28px;
  line-height: 27px;
  font-size: 14px;
  content: "\e81b";
  color: rgba(67, 82, 87, 0.5);
  font-family: "Fontello", sans-serif;
}
.navigationbar__search-input {
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: block;
  text-indent: none;
  font-weight: bold;
  position: relative;
  box-sizing: border-box;
  text-transform: uppercase;
  width: 100%;
  font-size: 13px;
  height: 28px;
  padding: 6px 34px;
  line-height: 16px;
  background-color: #34a569;
  color: #ffffff;
  border-radius: 2px 2px 2px 2px;
  text-shadow: 0 1px 0 rgba(8, 54, 65, 0.25);
  font-family: "Lato", sans-serif;
}
.navigationbar__search-input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.6);
  text-shadow: none;
}
.navigationbar__search-clear {
  display: block;
