/*
 * Modul: Play.
 * Definiert das Verhalten von Audio-Playern.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.play {
  cursor: pointer;
  font-size: 15px;
  transition: all 0.15s ease 0s;
}
.play:before {
  display: block;
  position: absolute;
  top: 0;
  content: "";
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("../img/spinner.gif?v=0.1");
  background-size: 16px 16px;
  opacity: 0;
  left: -8px;
  width: 16px;
  transition: opacity 0.15s ease 0s;
}
.play--playing:not(.play--loading) {
  color: #5e5c70;
  text-shadow: -2px -2px 6px #d0ccf8, -2px 2px 6px #d0ccf8, 2px -2px 6px #d0ccf8, 2px 2px 6px #d0ccf8;
}
.play--loading:before {
  opacity: 1;
}
.play__icon {
  font-size: 16px;
}
.play__audio {
  display: none;
}
/*
 * Modul: Fortschritts-Leiste.
 * Definiert das Verhalten vom Quiz-Fortschritt.
 * @author Jakob Metzger <jakob.me@gmail.com>
 * @copyright 2016 Jakob Metzger
 * @license MIT
 */
.progressbar {
  display: block;
  position: absolute;
  box-sizing: border-box;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  padding: 16px;
  background-color: #ffffff;
  color: rgba(67, 82, 87, 0.5);
  border-bottom: 1px solid #c8d3d5;
  border-top: 1px solid #c8d3d5;
}
.progressbar__wrapper {
  display: block;
  position: relative;
  height: 20px;
}
.progressbar__line {
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  z-index: 100;
  height: 2px;
  background: #c8d3d5;
  margin-top: -1px;
  border-radius: 2px 2px 2px 2px;
}
.progressbar__steps {
  display: block;
  position: absolute;
  list-style-type: none;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 500;
  height: 20px;
  line-height: 20px;
}
.progressbar__step {
  float: left;
  display: block;
  text-align: center;
  position: relative;
  margin: 0;
  padding: 0;
  height: 20px;
  width: 10%;
  transition: all 0.15s ease 0s;
}
.progressbar__icon {
  display: block;
  max-width: 100%;
  text-align: center;
  box-sizing: border-box;
  font-size: 10px;
  margin: 5px auto;
  width: 10px;
  height: 10px;
  border: 2px solid #c8d3d5;
  background: #ffffff;
  border-radius: 50% 50% 50% 50%;
  transition: all 0.3s ease 0s;
}
.progressbar__step--error .progressbar__icon,
.progressbar__step--current .progressbar__icon,
.progressbar__step--skipped .progressbar__icon,
.progressbar__step--success .progressbar__icon {
  margin: 0 auto;
  width: 20px;
  height: 20px;
  line-height: 16px;
}
.progressbar__step--skipped .progressbar__icon {
  background: transparent;
  box-shadow: inset 0 0 0 2px #ffffff;
}
.progressbar__step--error .progressbar__icon:after {
  content: "\e819";
  color: #d9426d;
}
.progressbar__step--success .progressbar__icon:after {
  content: "\e81a";
  color: #3bbb77;
}
.progressbar--animated .progressbar__step--success .progressbar__icon {
  animation: progress-step-success 0.15s normal forwards;
}
.progressbar--animated .progressbar__step--error .progressbar__icon {
  animation: progress-step-error 0.15s normal forwards;
}
