  left: 16px;
  right: 16px;
  line-height: 44px;
  font-size: 18px;
  box-shadow: 0 2px 2px 0 rgba(8, 54, 65, 0.05);
  background-color: #ffffff;
  color: #4b4e4f;
  border: 1px solid #c8d3d5;
  border-bottom-color: #bac7ca;
  transition: all 0.15s ease 0s;
  transform: translate3d(0, 0, 0);
