  content: "\e819";
  font-family: "Fontello", sans-serif;
}
.quiz__solution--error:after {
  color: #d9426d;
}
.quiz__solution--locked {
  opacity: 0;
  transform: translate3d(0, -100%, 0);
}
.quiz__letter {
  float: left;
  display: block;
  box-sizing: border-box;
  height: 42px;
  border-right: 1px solid #f1f3f4;
  transition: all 0.15s ease 0s;
}
.quiz__letter:last-child {
  border: none;
}
.quiz__action {
  -webkit-backface-visibility: hidden;
  position: absolute;
  box-sizing: border-box;
  left: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  z-index: 1000;
  padding: 16px;
  opacity: 1;
  transition: all 0.15s ease 0s;
  transform: translate3d(0, 0, 0);
}
.quiz__action--locked {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}
.quiz__action--hidden {
  display: none;
}
.quiz__submit {
  display: none;
}
